<template>
    <div class="main">
        <div class="preloader" v-show="preloader_show">
            <h1 :style="{color: '#3ad55c', textAlign: 'center', marginTop: '50%', fontSize: '80px', fontWeight: '900'}">+1</h1>
            <div id="fountainG">
                <div id="fountainG_1" class="fountainG"></div>
                <div id="fountainG_2" class="fountainG"></div>
                <div id="fountainG_3" class="fountainG"></div>
                <div id="fountainG_4" class="fountainG"></div>
                <div id="fountainG_5" class="fountainG"></div>
                <div id="fountainG_6" class="fountainG"></div>
                <div id="fountainG_7" class="fountainG"></div>
                <div id="fountainG_8" class="fountainG"></div>
            </div>
        </div>
        <div class="main-content">
            <div class="header" v-show="header_show">
                <img src="@/assets/images/1.png" @click="setting_func"/>
                <span v-show="time_show" >{{time}}</span>
            </div>
            <transition name="fade">
                <span v-if="text_start_1" class="text-hello">
                    Привет, я приложение <b>+1</b><br/>
                    Я, помогу твоему мозгу работать лучше

                    <button @click="button_next" class="button">Далее</button>
                </span>
            </transition>      
            <div class='app' :style="{width: '100%', display: 'flex', justifyContent: 'center', flexDirection: 'column'}" v-show="app_show">
                <div :style="{display: 'flex', justifyContent: 'center'}" v-show="number_block">
                    <div class="number" v-for="num in number" :key="Math.random()+num">
                        <span v-show="elem_number">{{num}}</span>
                    </div>
                </div>

                <div class="inp-number" v-show="input_number">
                    <div v-for="num in number" :key="Math.random()+num-1">
                        <input type="number" placeholder="_"/>
                    </div>
                </div>

                <div class='result' v-show="result">
                    Результаты
                </div>

                <div :style="{display: 'flex', flexDirection: 'column', width: '100%', position: 'absolute', bottom: '0', marginBottom: '40px'}" >
                    <button v-show="start_button" class="button" @click="start">Начать</button>
                    <button v-show="next_button" class="button" @click="next">Продолжить</button>
                    <button v-show="stop_button" class="button" @click="stop">Стоп</button>
                </div>
                
            </div>

            <div class="setting" v-show="setting">
                setting
            </div>
        </div>

    </div>
</template>

<script>
    export default {
        data() {
            return {
                preloader_show: true,
                text_start_1: true,

                header_show: false,

                number_block: true,

                start_button: true,
                stop_button: false,
                next_button: false,

                app_show: false,
                setting: false,

                result: false,

                count_number: 4,
                number: [],

                elem_number: true,
                input_number: false,

                time: 10,
                time_show: false,
            }
        },
        components: { 
        },
        methods: {
             button_next() {
                this.text_start_1 = !this.text_start_1;
                 setTimeout(() => {
                    this.app_show = !this.app_show;
                    this.header_show = !this.header_show;
                }, 1000)
            },

            setting_func() {
                this.setting = !this.setting;
                this.app_show = !this.app_show;
            },

            next() {
                this.time_show = true;

                this.number = []

                this.elem_number = true

                this.input_number = false;

                this.time = 10

                for (let i=0; i<this.count_number; i++) {
                    this.number.push(Math.floor(Math.random() * Math.floor(9)))
                }

                setTimeout(() => {
                    this.elem_number = false
                    this.input_number = true;

                    let int = setInterval(() => {
                        this.time-=1
                        if (this.time == 0) {
                            this.next()
                            clearTimeout(int)
                        }
                    }, 1000)

                }, 2000)

            },

            start() {
                this.start_button = !this.start_button;
                this.next_button = !this.next_button;
                this.stop_button = !this.stop_button;
                this.app_show = true;
                this.number_block = true;
                this.result = false;
                this.next()

            },

            stop() {
                this.start_button = true;
                this.result = true;
                this.elem_number = true
                this.next_button = false;
                this.stop_button = false;
                this.number_block = false;
                this.time_show = false;
                this.input_number = false;
            },
        
        },
        mounted() {
            setTimeout(() => {
                this.preloader_show = !this.preloader_show;
            }, 2000)
        }
    }
</script>

